.checkoutProduct {
  display: flex;
  margin-bottom: 20px;
  margin-top: 20px;
}

.checkoutProduct__image {
  object-fit: contain;
  width: 180px;
  height: 180px;
}

.checkoutProduct__info{
    padding-left: 20px;
}

.checkoutProduct__title{
    font-size: 17px;
    font-weight: 800;
}

.checkoutProduct__rating{
    display: flex;
}

.checkoutProduct__info>button{
    background: #f0c14b;
    border-color: #a88734 #9c7e31 #846a29;
    margin-top: 10px;
    border: 1px solid;
    color: #111;
}